:root {
  font-size: 16px;
  font-family: "Open sans";
  --text-primary: #1f1f1f;
  --text-secondary: #000000;
  --bg-primary: #ffffff;
  --bg-secondary: #e4e4e4;
  --transition-speed: 200ms;
}

body {
  color: black;
  background-color: white;
  margin: 0;
  padding: 0;
}

body::-webkit-scrollbar {
  width: 0.25rem;
}

body::-webkit-scrollbar-track {
  background: #1e1e24;
}

body::-webkit-scrollbar-thumb {
  background: #6649b8;
}

.main {
  margin-left: 5rem;
}

.navbar {
  position: fixed;
  background-color: var(--bg-primary);
  transition: width 200ms ease;
  border: 0.3rem solid var(--bg-secondary);
}

.navbar-nav {
  list-style: none;
  height: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav-item {
  width: 100%;
}

.nav-item:last-child {
  margin-top: auto;
}

.nav-link {
  display: flex;
  align-items: center;
  justify-items: center;
  text-align: center;
  height: 5rem;
  color: var(--text-primary);
  text-decoration: none;
  filter: grayscale(100%) opacity(0.7);
  transition: var(--transition-speed);
}

.link-text {
  display: none;
  margin-left: 1rem;
}

.nav-link svg {
  width: 2rem;
  min-width: 2rem;
  margin: 0 1.5rem;
}

.active {
  background: var(--bg-secondary);
  filter: grayscale(0);
}

.fa-primary {
  color: #ff7eee;
}

.brand {
  filter: grayscale(0);
  background-color: var(--bg-secondary);
  color: rgb(34, 128, 206);
}

.panel {
  height: 100%;
  width: 20rem;
  position: fixed;
  top: 0;
  background-color: var(--bg-primary);
  border: 1rem solid var(--bg-secondary);
  z-index: 0;
  display: block;
}

.board {
  height: 100vh;
  width: 69%;
  position: fixed;
  top: 0;
  margin-left: 22rem;
  background-color: var(--bg-primary);
  border-top: 1rem solid var(--bg-secondary);
  border-bottom: 1rem solid var(--bg-secondary);
  border-right: 1rem solid var(--bg-secondary);
  z-index: -1;
}

.upload {
  display: flex;
  width: 16rem;
  height: 3rem;
  position: fixed;
  margin: 2rem;
  justify-content: center;
}

.buttonUpload {
  width: 15rem;
  background: orange;
  border: 0.2rem solid var(--bg-secondary);
  border-radius: 0.5rem;
  font-size: 1rem;
  align-items: center;
  text-align: center;
  padding: 0.7rem;
}

.addGraph {
  top: 5rem;
  width: 20rem;
  height: 3rem;
  margin-top: 2rem;
  position: relative;
  justify-content: center;
  align-items: center;
  display: flex;
  color: var(--text-primary);
  background: var(--bg-secondary);
}

.graphPanel {
  top: 2rem;
  width: 20rem;
  height: calc(100% - 10rem);
  margin-top: 3rem;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  overflow: auto;
}

::-webkit-scrollbar {
  width: 0.1rem;
}

/* .graphPanel::-webkit-scrollbar-track {
  background: #1e1e24;
}

.graphPanel::-webkit-scrollbar-thumb {
  background: #1e1e24;
} */

.panelItem {
  width: 16rem;
  height: 6rem;
  padding: 1rem;
  margin: 1rem;
  color: var(--text-primary);
  background: var(--bg-secondary);
  border-radius: 1rem;
}

.panelName {
  text-align: center;
  border: none;
  outline: none;
  background-color: transparent;
  font-size: 1rem;
  color: var(--text-secondary);
  margin-bottom: 0.6rem;
}

.canvas {
  height: 90vh;
}

/* .line {
  fill: none;
  stroke-width: 3;
} */

.axis {
  color: var(--text-primary);
}

.legendText {
  fill: var(--text-primary);
}

.svgNoDisplay {
  display: none;
}

.downloadButton {
  float: right;
  width: 10rem;
  height: 3rem;
  display: flex;
  background: orange;
  border: 0.2rem solid var(--bg-secondary);
  align-items: center;
  margin-right: 2rem;
  border-radius: 0.5rem;
  justify-content: center;
}

button:hover {
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .navbar {
    bottom: 0;
    height: 5rem;
    width: 100vw;
    display: flex;
  }

  .navbar-nav {
    flex-direction: row;
  }

  .nav-link {
    justify-content: center;
  }

  .main {
    margin: 0;
  }
}

@media only screen and (min-width: 600px) {
  .navbar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }

  /* .navbar:hover {
    width: 16rem;
    font-size: large;
  }

  .navbar:hover .link-text {
    display: block;
    transition: opacity var(--transition-speed) ease;
  }

  .navbar:hover ~ .main {
    margin-left: 16rem;
    transition: margin var(--transition-speed) ease;
  }*/

  .buttonUpload:hover {
    filter: grayscale(0) drop-shadow(0px 0px 10px black);
    transition: filter var(--transition-speed) ease-in-out;
    cursor: pointer;
  }

  .nav-link:hover {
    filter: grayscale(0) opacity(1);
    background: var(--bg-secondary);
    color: var(--text-secondary);
  }

  .addGraph:hover {
    transform: scale(1.05);
    transition: 200ms ease-in-out;
    cursor: pointer;
  }
}

.dark {
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
}

.light {
  --text-primary: #1f1f1f;
  --text-secondary: #000000;
  --bg-primary: #ffffff;
  --bg-secondary: #e4e4e4;
}

.solar {
  --text-primary: #576e75;
  --text-secondary: #35535c;
  --bg-primary: #fdf6e3;
  --bg-secondary: #f5e5b8;
}
